.section2{
    background: linear-gradient(180deg, #83359C 0%, #20E0EA 100%);
    background-repeat: no-repeat;
    height :1024px;
/*     border:5px solid #06a340;
 */}
.section2 p {
    color: #FFF;
text-align: center;
font-family: Righteous;
font-size: 45px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 2.025px;
}
.section2 .cards {
 display: grid;
  grid-template-columns: repeat(auto-fill,minmax(300px, 1fr));
 gap: 20px;
  /* border: 2px solid #000; */

}
.section2 .card .title{
    color: #FFF;
text-align: center;
font-family: Righteous;
font-size: 30px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 1.35px;
}
.section2 .card img{
    width:100%;
    height: 100%;
    
}
.section2 .card .flovo{
   width:100%;
    height: 100%;
    border-radius: 25px;
background: #FFC244;
display: flex;
justify-content: center;
align-items: center;
    color: #00A082;
text-align: center;
font-family: Righteous;
font-size: 50px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 3.15px;
}
.section2 .card .bumia{
    width:100%;
    height: 100%;
    border-radius: 25px;
    background: #F68B1E;
    display: flex;
justify-content: center;
align-items: center;
color: #FFF;
text-align: center;
font-family: Lalezar;
font-size: 50px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 3.15px;
}
.section2 .card .airvnv{
       width:100%;
    height: 100%;
    border-radius: 25px;
    background:#FF385C;
    display: flex;
justify-content: center;
align-items: center;
color: #FFF;
text-align: center;
font-family: Days One;
font-size: 50px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 3.15px;
}
.btn{
    width: 110px;
height: 30px;
border-radius: 25px;
background: #83359C;
border: none;
color: #FFF;
text-align: center;
font-family: Righteous;
font-size: 20px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.9px;
}
.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
height: 320px;
border-radius: 60px;
background: var(--second-color, #11476A);
}
.card:hover{
    opacity: 0.5;
    cursor: pointer;
}


@media (max-width:768px){

.section2 .cards{
    margin: 0 auto;
}
.card{
    max-width:450px;
}
}
@media (max-width:480px){
   


.section2{
    height:2679px;
}
.section2 p {
    font-size: 43px;
}
.section2 .cards{
    padding-left: 10px;
    padding-right: 10px;
}
}