@import url('https://fonts.googleapis.com/css2?family=Days+One&family=Lalezar&family=Righteous&display=swap');

header{
    display: flex;
    justify-content: center;
    align-items: center;
   background: #172746;
    border:none;
   /*  border: 5px solid #06a340; */
    margin:0; 
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap:20px;
    margin-top:20px;
    background: #172746; 
    border:none;
    
}
