.logo{
  /* border: 2px solid #00A082; */
  width: 100%;
   height: 100%;
}
.logo p{
color: #FFF;
/* text-align: center; */
font-family: 'Righteous', sans-serif;
font-size: 40px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 1.8px;
}
.dev{
    color: #25C1D5;

}
.navbox{
   display: flex; 
   justify-content: center;
    align-items: center;
/* border:2px solid #a30606; */
 padding-left: 20px;
 padding-right: 20px;
height: 80px;
border-radius: 60px;
background: linear-gradient(180deg, rgba(72, 40, 124, 0.40) 63.02%, rgba(165, 123, 234, 0.26) 100%);
}

.navbox_menu{
    display: inline-flex;
align-items: center;
gap: 20px;
}
.navbox_menu a{
    color: #FFF;
text-align: center;
font-family: Righteous;
font-size: 45px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 2.025px;
text-decoration: none;
}
.header a:hover,
.header a:focus {
  color: #25C1D5;
} 
.lang{
    display: flex;
    flex-direction: column;
}
.lang a{
color: #FFF;
text-align: center;
font-family: Righteous;
font-size: 20px;
font-style: normal;
font-weight: 400;
line-height: normal;
letter-spacing: 0.9px;
text-decoration: none;
}
.hamburger{
    display: none;
}
@media (max-width:1200px){
    .container{
          max-width: 1000px;
    width:100%;
    }

   
   .header{
        max-width: 1100px;
        width: 100%;
    }
  .navbox{
        display:none;
    }
   .lang{
        display: none;
    }
   .hamburger{
    display: block;
}

}
@media (max-width:1024px){

.logo p{
    font-size: 35px;
}
.header{
    max-width: 900px;
    width: 100%;
}
.container{
    padding-left: 20px;
    padding-right: 20px;
}

}
@media (max-width:768px){


.logo p{
    font-size: 30px;
}
.header{
    max-width: 700px;
    width: 100%;
}


}
@media (max-width:480px){
   
    .logo p{
    font-size: 26px;
   }
header{
    width:100%;
     height:70px;
    position: fixed;
    z-index: 2;
}

.header{
  padding-left: 20px;
    padding-right: 20px;
}

}